/* You can add global styles to this file, and also import other style files */
:root {
  --gray-one:#F2F3F7;
  --gray-two:#15223214;
  --gray-three:#C5CBDA;
  --gray-black:#767981;
  --white:#FFFFFF;
  --bone:#0000001A;
  --piel:#FDEAE8;
  --red:#E63020;
  --black:#000000;
  --green:#25D366;
}

.mr-11 {
  margin-right: 0.6875rem;
}

.mr-12 {
  margin-right: 0.75rem;
}

.ml-11 {
  margin-left: 0.6875rem;
}

.mr-21 {
  margin-right: 1.3125rem;
}

.mr-20 {
  margin-right: 1.25rem;
}

.cursor-pointer {
  cursor: pointer;
}

.content-margin {
  margin-bottom: 4.6875rem;
  margin-top: 7.5rem;
}

.bg-gray1 {
  background-color: var(--gray-one);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  width: 90px;
}

.font-color {
  color: var(--gray-three);
}

.font1 {
  font-family: "Roboto";
  font-weight: bold;
}

.font2 {
  font-family: "Roboto";
  font-weight: 600;
}

.wii-button-exit {
  background-color: var(--white);
  border-radius: 50px;
  width: 88px;
  height: 40px;
  border: 2px solid var(--red);
  color: var(--red);
  font-size: 14px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.wii-button-exit i {
  width: 16px;
  height: 16px;
}

.wii-button-exit:hover {
  background-color: var(--red);
  color: var(--white);
}

.wii-button-actions {
  background-color: var(--red);
  border-radius: 50px;
  width: 283px;
  height: 40px;
  border: none;
  font-size: 14px;
  color: var(--white);
}

.wii-button-nav {
  color: var(--red);
  width: 18px;
  height: 21px;
}

.wii-button-close {
  color: var(--red);
  width: 12px;
  height: 21px;
}

.wii-button-new {
  background-color: var(--green);
  border-radius: 3.125rem;
  border: none;
  width: 6.25rem;
  height: 30px;
  color: var(--white);
  font-weight: bold;
  font-size: 0.75rem;
  font-family: "Poppins";
}

.wii-button-new {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.wii-button-outline {
  background-color: var(--white);
  border-radius: 3.125rem;
  border: 1px solid var(--red);
  width: 100%;
  padding: 8px 15px;
  color: var(--red);
  font-weight: bold;
  font-size: 0.75rem;
}

.wii-button-solid {
  background-color: var(--green);
  border-radius: 3.125rem;
  border: none;
  width: 100%;
  padding: 8px 15px;
  color: var(--white);
  font-weight: bold;
  font-size: 0.75rem;
}

button.ant-dropdown-trigger {
  background-color: var(--red);
  border-radius: 3.125rem;
  border: none;
  height: 2.5rem;
  align-items: center;
  width: 100%;
  color: var(--white);
  font-weight: 400;
  display: flex;
  text-align: center;
  justify-content: center;
  position: relative;
  padding-right: 2.5rem;
  padding-left: 1.25rem;
}
button.ant-dropdown-trigger .wii-icon {
  height: 2.5rem;
  width: 2.5rem;
  position: absolute;
  right: 0;
}

button.ant-dropdown-trigger:hover {
  background-color: var(--white);
  border: 0.0625rem solid var(--red);
  color: var(--red);
  transition: all 0.1s;
  font-weight: 400;
}

.wii-logo {
  width: 166px;
  height: 39px;
}

.wii-icon {
  padding: 0.9375rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wii-icon i, .wii-icon svg {
  font-size: 1.25rem;
  width: 1.25rem;
}

.wii-icon-primary {
  color: var(--red);
}

body {
  background: #F2F3F7 0% 0% no-repeat padding-box !important;
  height: 100vh;
}